import lazyLoading from './lazyLoading'

export default {
  name: '系统',
  path: '/charts',
  meta: {
    icon: 'fa-bar-chart-o',
    expanded: false
  },
  component: lazyLoading('charts', true),

  children: [
    {
      name: '人工智能模块',
      path: 'Chartjs',
      component: lazyLoading('charts/Chartjs')
    },
    {
      name: 'Maze中心',
      path: 'peity',
      component: lazyLoading('charts/Peity')
    }
  ]
}



